<script>

import TopNav from '@/components/TopNav.vue'
import BotNav from '@/components/BotNav.vue'

export default {
    name : "Shop" ,
    components : {
        TopNav, 
        BotNav
    },
    data () {

      return {

       inputLeft : document.getElementById("input-left"),
       inputRight : document.getElementById("input-right"),

        thumbLeft : document.querySelector(".slider > .thumb.left"),
        thumbRight : document.querySelector(".slider > .thumb.right"),
        range : document.querySelector(".slider > .range"),
      }
    },
    methods : {
        openFunction(){
        document.getElementById("filtermenu").style.width="300px";
        console.log(this.$route.name);
        // document.getElementById("mainbox").style.marginLeft="300px";
        // document.getElementById("mainbox").innerHTML="Click on Cross Element and Close Menu";
      },
        closeFunction(){
        document.getElementById("filtermenu").style.width="0px";
        document.getElementById("mainbox2").style.marginLeft="0px";
        // document.getElementById("mainbox").innerHTML="&#9776; Open";
    },
      setLeftValue() {
      var _this = this.inputLeft,
        min = parseInt(_this.min),
        max = parseInt(_this.max);

      _this.value = Math.min(parseInt(_this.value), parseInt(this.inputRight.value) - 1);

      var percent = ((_this.value - min) / (max - min)) * 100;

      this.thumbLeft.style.left = percent + "%";
      this.range.style.left = percent + "%";
    },
      setRightValue() {
      var _this = this.inputRight,
        min = parseInt(_this.min),
        max = parseInt(_this.max);

      _this.value = Math.max(parseInt(_this.value), parseInt(this.inputLeft.value) + 1);

      var percent = ((_this.value - min) / (max - min)) * 100;

      this.thumbRight.style.right = (100 - percent) + "%";
      this.range.style.right = (100 - percent) + "%";
    },

    },
    mounted (){
      this.setLeftValue();

        this.setRightValue();
    },
    created () {

        

          this.inputLeft.addEventListener("input", this.setLeftValue);
          this.inputRight.addEventListener("input", this.setRightValue);

          this.inputLeft.addEventListener("mouseover", function() {
            this.thumbLeft.classList.add("hover");
          });
          this.inputLeft.addEventListener("mouseout", function() {
            this.thumbLeft.classList.remove("hover");
          });
          this.inputLeft.addEventListener("mousedown", function() {
            this.thumbLeft.classList.add("active");
          });
          this.inputLeft.addEventListener("mouseup", function() {
            this.thumbLeft.classList.remove("active");
          });

          this.inputRight.addEventListener("mouseover", function() {
            this.thumbRight.classList.add("hover");
          });
          this.inputRight.addEventListener("mouseout", function() {
            this.thumbRight.classList.remove("hover");
          });
          this.inputRight.addEventListener("mousedown", function() {
            this.thumbRight.classList.add("active");
          });
          this.inputRight.addEventListener("mouseup", function() {
            this.thumbRight.classList.remove("active");
          });

    }
}
</script>

